import { SIMPLE_TAB_HEADER_STYLES, SIMPLE_TAB_STYLES } from "assets/base-theme";
import BaseTab from "components/base/BaseTab";
import { ISimpleTabProps } from "components/interface/Input";
import React from "react";
import { generateClasses } from "utils/Util";

const SimpleTab: React.FC<ISimpleTabProps> = React.forwardRef((props: any, ref: any) => {
  const getClassNames = () => {
    const styleObject = {
      ...SIMPLE_TAB_STYLES,
      overrideStyle: props.styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      isInvalid: !!props.validation,
      removeStyleProperty: [],
      removeClasses: [],
    };

    return generateClasses(styleObject, props.variant);
  };

  const getHeaderClassNames = () => {
    const styleObject = {
      ...SIMPLE_TAB_HEADER_STYLES,
      overrideStyle: props.headerStyleObj,
      overrideClasses: props.headerClassName,
      removeStyleProperty: [],
      removeClasses: [],
    };

    return generateClasses(styleObject, props.headerVariant);
  };

  const {
    className,
    value,
    onChange,
    name,
    disabled,
    tabs,
    onTabChange,
    activeIndex,
    onBeforeTabChange,
    showTooltip,
  } = props;

  return (
    <BaseTab
      {...props}
      ref={ref}
      tabs={tabs}
      className={className ?? ""}
      value={value}
      onChange={onChange}
      name={name}
      disabled={disabled}
      headerClassName={getHeaderClassNames()}
      onTabChange={onTabChange}
      activeIndex={activeIndex}
      onBeforeTabChange={onBeforeTabChange}
      showTooltip={showTooltip}
    />
  );
});

export default React.memo(SimpleTab);
