import MenuBox from "components/FileUploadMenuBox";
import Text from "components/Text";
import { ISimpleTabProps } from "components/interface/Input";
import { TabPanel, TabView } from "primereact/tabview";
import React from "react";
import { useTranslation } from "react-i18next";

const BaseTab: React.FC<ISimpleTabProps> = React.forwardRef((props: any, ref: any) => {
  const { renderContent, headerClassName, tabs, ...baseProps } = props;
  const {
    onTabChange,
    activeIndex,
    onBeforeTabChange,
    showTooltip,
    scrollable,
    className,
    optionLabel,
    optionKey,
    disabledTabMenu,
    tabHeaderMenuModel,
    enableTabMenu,
    ...primeReactProps //NOSONAR
  } = baseProps;

  const { t } = useTranslation(); //NOSONAR
  const content: any[] = [];

  function getTabKey(tab: any) {
    let key = "key"; // Default key value

    if (Array.isArray(optionKey)) {
      for (const keyName of optionKey) {
        if (tab[keyName]) {
          key = keyName; // If the key is found in tab, set it as the value of 'key'
          break; // Exit the loop once a match is found
        }
      }
    } else {
      key = tab[optionKey];
    }

    return key;
  }

  tabs.forEach((tab: any, index: any) => {
    let title = "";
    if (Array.isArray(optionLabel)) {
      optionLabel.forEach((name: string) => {
        if (tab[name]) {
          title += " " + tab[name];
        }
      });
    } else {
      title = tab[optionLabel];
    }

    // to set the dynamic key value to the tab
    // do not touch the key code it will impact all the functionalities
    // Todo ---- need more flexibility ( make key as a unique for tab )
    if (Array.isArray(optionKey)) {
      for (const keyName of optionKey) {
        if (tab[keyName]) {
          tab.key = tab[keyName]; // If the key is found in tab, set it as the value for tab 'key'
          break; // Exit the loop once a match is found
        }
      }
    } else {
      tab.key = tab[optionKey];
    }

    let header = (
      <div className="flex relative tab-header-main">
        <div className="flex items-center space-x-1">
          <div className="flex items-center outline-none " title={showTooltip ? title : ""}>
            <Text
              label={title}
              className={`text-xs font-Roboto font-medium inline-block cursor-pointer simple-tab-header ${
                tab?.disabled && index !== activeIndex ? "text-secondary-steelBlue-400" : ""
              }`}
            />
          </div>

          {enableTabMenu ? (
            <MenuBox
              model={tabHeaderMenuModel(tab, index)}
              variant="SECONDARY"
              disabled={disabledTabMenu}
              buttonClassName="!bg-transparent"
            />
          ) : null}
        </div>
      </div>
    );
    if (tab.headerLabelTemplate) {
      header = tab.headerLabelTemplate;
    }
    content.push(
      <TabPanel
        header={header}
        // key={getTabKey(tab)}
        key={tab.key}
        headerClassName={headerClassName}
        disabled={!!tab.disabled}
      >
        {renderContent(tab, index)}
      </TabPanel>
    );
  });

  return (
    <TabView
      className={className}
      onTabChange={onTabChange}
      activeIndex={activeIndex}
      onBeforeTabChange={onBeforeTabChange}
      scrollable={scrollable}
      ref={ref}
    >
      {content}
    </TabView>
  );
});

BaseTab.defaultProps = {
  optionLabel: "name",
  optionKey: "key",
  disabledTabMenu: false,
};

export default BaseTab;
